import * as actions from '../actions'

const initialState = {}

const tables = (state = initialState, action) => {
    switch (action.type) {
    case actions.TableActions.GET_TABLE:
        return Object.assign({}, {
            ...state,
            [`${action.tableName}`]: {
                url: action.url,
                page: action.page,
                sort: action.sort,
                order: action.order,
            },
        })
    case actions.TableActions.RECEIVE_TABLE:
        return Object.assign({}, {
            ...state,
            [`${action.tableName}`]: {
                data: action.data,
                ...state[action.tableName],
            },
        })
    default:
        return Object.assign({}, state)
    }
}

export default tables
