export const GET_TABLE = 'GET_TABLE'
export const RECEIVE_TABLE = 'RECEIVE_TABLE'

function action(type, payload = {}) {
    return { type, ...payload }
}

const getTable = (tableName, url, page, sort, order, perPage) => action(GET_TABLE, {
    tableName, url, page, sort, order, perPage,
})

const receiveTable = (tableName, data) => action(RECEIVE_TABLE, { tableName, data })

export default {
    getTable,
    receiveTable,
}
